.inner {
	max-width: 1140px;
	padding: 0 20px;
	margin: 0 auto;
	@include clr; }
.breadcrumbs {
	padding: 30px 0; }
.breadcrumbs-list {
	position: relative;
	font-size: 12px;
	&__item {
		display: inline-block;
		vertical-align: middle;
		@include light;
		&:first-of-type {
			a {
				position: relative;
				padding-left: 20px;
				&:before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					top: 0;
					@include tr(opacity 0.3);
					@include s(home); } } }
		a {
			font-size: inherit;
			font-family: inherit;
			cursor: pointer;
			display: inline-block;
			&:hover {
				text-decoration: underline;
				&:before {
					opacity: 0.8; } } }
		span {
			@include bold; } } }
.slider {
	margin: 0 -14px;
	.slick-list {
		padding: 30px 0 90px; }
	&__item {
		padding: 12px 20px 60px;
		position: relative;
		background-color: #fff;
		box-shadow: 0px 2px 15px 0px rgba(0, 0, 0, 0.15);
		border-radius: 6px;
		min-height: 376px;
		font-size: 16px; }
	.slick-arrow {
		@include hide-text; }
	.slick-prev,
	.slick-next {
		width: 44px;
		height: 91px;
		background: url(../img/arrow-left-bg.png) no-repeat center;
		top: 31.5%;
		left: -3px;
		position: absolute;
		z-index: 10;
		&:before {
			content: '';
			display: block;
			@include s(arrow-left);
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			margin: auto; } }
	.slick-next {
		background: url(../img/arrow-right-bg.png) no-repeat center;
		left: auto;
		right: -3px;
		&:before {
			@include s(arrow-right); } }
	.slick-dots {
		font-size: 0;
		position: absolute;
		left: 0;
		right: 0;
		text-align: center;
		bottom: 50px;
		button {
			@include hide-text;
			width: 10px;
			height: 10px;
			border-radius: 50%;
			background-color: transparent;
			border: 1px solid #b8b8b8;
			transition: background-color 0.3s, border-color 0.3s; }
		li {
			display: inline-block;
			vertical-align: middle;
			padding: 0 5px;
			&.slick-active {
				button {
					background-color: #ff9524;
					border-color: #ff9524; } } } }
	.btn-cart {
		float: right; } }

.item {
	margin: 0 14px;
	&__media {
		height: 191px;
		position: relative;
		overflow: hidden;
		img {
			max-width: 100%;
			max-height: 100%;
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			margin: auto; } }
	&__description {
		height: 70px;
		overflow: hidden;
		margin: 5px 0 0; }
	&__price {
		@include bold;
		@include text-overflow; }
	&__footer {
		position: absolute;
		left: 20px;
		right: 20px;
		bottom: 20px;
		@include clr; } }
