//TT Prosto Sans
@include font("TT Prosto Sans Black", "tt_prosto_sans_black");
@mixin black {
	font-family: "TT Prosto Sans Black", Arial, Helvetica, sans-serif;
	font-weight: normal; }

@include font("TT Prosto Sans Bold", "tt_prosto_sans_bold");
@mixin bold {
	font-family: "TT Prosto Sans Bold", Arial, Helvetica, sans-serif;
	font-weight: normal; }

@include font("TT Prosto Sans Regular", "tt_prosto_sans_regular");
@mixin reg {
	font-family: "TT Prosto Sans Regular", Arial, Helvetica, sans-serif;
	font-weight: normal; }

@include font("TT Prosto Sans Light", "tt_prosto_sans_light");
@mixin light {
	font-family: "TT Prosto Sans Light", Arial, Helvetica, sans-serif;
	font-weight: normal; }
