//vars
$gray: #f2f2f2;
$orange: #ff8400;
$black: #000;
$white: #fff;
$secondary-color: #646464;

//icons
[class*="icon"] {
	display: inline-block;
	vertical-align: middle; }
.icon-catalog {
	@include s(catalog); }
.icon-user {
	@include s(user); }
.icon-cart {
	@include s(cart); }
.icon-fb {
	@include s(fb); }
.icon-tw {
	@include s(tw); }
.icon-ins {
	@include s(ins); }
.icon-webmoney {
	@include s(webmoney); }
.icon-paypal {
	@include s(paypal); }
.icon-qiwi {
	@include s(qiwi); }
.icon-mastercard {
	@include s(mastercard); }
.icon-visa {
	@include s(visa); }
.icon-phone {
	@include s(phone); }
.icon-location {
	@include s(location); }
.icon-check {
	@include s(check); }

body {
	background: $white;
	font-size: 14px;
	line-height: 1.4;
	@include reg; }
a {
	text-decoration: none;
	color: #646464; }

.title {
	@include bold;
	font-size: 24px;
	color: #000;
	text-align: center; }

.btn {
	display: inline-block;
	padding: 5px 10px;
	vertical-align: top;
	text-align: center;
	color: $black;
	background-color: $gray;
	border-radius: 6px;
	height: 40px;
	cursor: pointer;
	position: relative;
	text-transform: uppercase;
	@include bold;
	font-size: 14px;
	@include tr(background-color 0.3s);
	span {
		display: inline-block;
		vertical-align: middle; }
	&:active {
		top: 1px; } }
.btn-catalog {
	padding: 5px 19px;
	margin: 30px 0 0;
	color: $white;
	background-color: $orange;
	&:hover {
		background-color: lighten($orange, 10%); }
	span {
		margin-left: 5px; } }
.btn-login,
.btn-cart {
	&:hover {
		background-color: lighten($gray, 1%); } }
.btn-login {
	padding: 5px 15px;
	margin-right: 20px;
	i {
		margin: 0 2px 0 0; } }
.btn-cart {
	padding: 5px 22px;
	&_orange {
		background-color: $orange;
		color: #fff;
		padding: 5px 17px;
		&:hover {
			background-color: lighten($orange, 5%); } }
	i {
		margin: 0 2px 0 0; } }
.input {
	display: block;
	width: 100%;
	box-shadow: inset 1px 2px 2px rgba(0, 0, 0, 0.15);
	border-radius: 6px;
	background-color: $gray;
	@include reg;
	font-size: 14px;
	color: $black;
	padding: 11px 40px 11px 20px;
	&:focus {
		box-shadow: inset 1px 2px 2px rgba(0, 0, 0, 0.15), 1px 2px 2px rgba(0, 0, 0, 0.15); }
	@include placeholder {
		color: $black; }
	&::-webkit-search-cancel-button {
		-webkit-appearance: none; } }
.search {
	position: relative;
	margin: 20px 0 0;
	max-width: 537px;
	&__btn {
		display: block;
		position: absolute;
		top: 0;
		right: 17px;
		bottom: 0;
		margin: auto;
		@include s(search);
		background-color: transparent; } }
